.shopping-list__heading-section {
  display: flex;
  width: 100%;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.shopping-list__edit-heading-form {
  display: flex;
  flex-wrap: nowrap;
  border-radius: 12px;
  justify-content: space-between;
}

.shopping-list__edit-heading-form:focus-within {
  outline: none;
  -webkit-appearance: none;
  box-shadow: -0.3px -0.3px 2px 2px var(--orange-color);
  background-color: var(--white-color);
}

.shopping-list__edit-heading-form_disabled:focus-within {
  outline: 2px solid var(--grey-color2);
  outline-offset: -2px;
}

.shopping-list__heading {
  margin: 0;
  font-size: 24px;
  line-height: 1.25;
  font-weight: 700;
  color: var(--dark-brown-color);
  border-radius: 12px;
  width: calc(100% - 35px);
  background-color: var(--light-orange-color);
  border: none;
  text-align: center;
}

.shopping-list__heading:focus {
  outline: none;
  background-color: var(--white-color);
}

.shopping-list__heading:hover {
  cursor: pointer;
}

.shopping-list__edit-heading-btn {
  border-radius: 12px;
  background-color: var(--orange-color);
  border: none;
  color: var(--white-color);
  width: 50px;
  font-size: 14px;
  transition: all 0.3ms ease-out;
  display: none;
}

.shopping-list__edit-heading-btn_visible {
  display: block;
}

.shopping-list__edit-heading-btn_disabled {
  background-color: var(--grey-color2);
}

.shopping-list__edit-heading-btn:hover {
  cursor: pointer;
  opacity: 0.7;
}

.shopping-list__heading-error {
  color: var(--red-color);
  font-size: 10px;
  line-height: 1.25;
  height: 12px;
}

.shopping-list__buttons {
  display: flex;
}

.shopping-list__button {
  width: 25px;
  height: 25px;
  margin-left: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: var(--light-orange-color);
  border: none;
  transition: all 0.3ms ease;
  align-self: center;
  padding: 0;
}

.shopping-list__button:hover {
  cursor: pointer;
  opacity: 0.7;
}

.shopping-list__button_complete {
  background-image: url("../../images/check_circle_FILL0_wght700_GRAD0_opsz48.svg");
}

.shopping-list__button_copy {
  background-image: url("../../images/edit_FILL0_wght700_GRAD0_opsz48.svg");
  position: relative;
}

.shopping-list__button_delete-list {
  background-image: url("../../images/delete_FILL0_wght400_GRAD0_opsz48.svg");
}

.shopping-list__button_copy_active::before {
  position: absolute;
  top: -45px;
  left: -100px;
  content: attr(title);
  width: 150px;
  height: 40px;
  background-color: var(--grey-color);
  color: var(--dark-brown-color);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.4;
  border-radius: 4px;
  text-align: center;
  visibility: visible;
  z-index: 1;
}
.shopping-list__edit-btn:hover {
  cursor: pointer;
  opacity: 0.7;
}

@media screen and (max-width: 1005px) {
  .shopping-list__heading {
    font-size: 20px;
  }
}
@media screen and (max-width: 375px) {
  .shopping-list__heading {
    font-size: 14px;
  }
  .shopping-list__button {
    width: 20px;
    height: 20px;
  }
}
