.category__item {
  list-style: none;
  display: flex;
  justify-content: space-between;
  background-color: var(--white-color);
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  align-items: center;
  padding: 0 15px;
}

.category__item:hover {
  cursor: pointer;
}

.category__item-link {
  display: flex;
}

.category__item-name {
  font-size: 14px;
  line-height: 1.25;
  max-width: 120px;
  /*text-overflow: ellipsis;*/
  /*white-space: nowrap;*/
  /*overflow: hidden;*/
  /*overflow-wrap: break-word;*/
  /*white-space: normal;*/
  color: black;
  text-decoration: none;
}

.category__item-button {
  width: 18px;
  height: 18px;
  padding: 0;
  border: none;
  background-color: var(--white-color);
  background-image: url("../../images/add_FILL0_wght400_GRAD0_opsz48.svg");
  background-position: center;
  background-size: cover;
  transition: all 0.3ms ease;
}

.category__item-button:hover {
  cursor: pointer;
  opacity: 0.7;
}

@media screen and (max-width: 1005px) {
  .category__item {
    padding: 0 10px;
    max-width: 75px;
  }
  .category__item-name {
    font-size: 12px;
  }
}

@media screen and (max-width: 425px) {
  .category__item {
    padding: 0 10px;
  }
  .category__item-button {
    width: 15px;
    height: 15px;
  }
  .category__item-name {
    width: 50px;
    font-size: 12px;
    /*text-overflow: unset;*/
    /*white-space: normal;*/
    /*overflow: unset;*/
    /*overflow-wrap: break-word;*/
  }
}
